@import '~node_modules/modern-normalize/modern-normalize.css';
html{
    box-sizing: border-box;
}
body{
    font-family: 'Plus Jakarta Sans', sans-serif;
    color:#ffffff;
    background-color: #ffffff;
    font-style: normal;
    letter-spacing: 0.03em;
    margin: 0 auto;
}
ul{
    padding-inline-start: 0;
}
img{
    display: block;
    width: 100%;
    height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin-top: 0;
}
.list{
    list-style: none;
    margin: 0;
}
.link{
    text-decoration: none;
}
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }